import Modal from './confirm'

function getModalInstance(props) {
  return Modal.newInstance(props)
}

function confirm(options) {
  let instance = getModalInstance(options)

  options.onRemove = function () {
    instance = null
  }

  instance.show(options)
}

// Modal下的静态alert confirm只提供简单的确认取消,如果需要严格控制流程,如提交回调,promise处理,请使用Modal本身处理

Modal.alert = function (_props = {}) {
  return confirm(_props)
}

Modal.confirm = function(_props = {}) {
  let props = Object.assign(_props, {
    confirmModal: true
  })
  return confirm(props)
}

export default Modal
export {
  Modal
}



